<template>
	<div class="my-bank-card-credit-detail">
		<mt-cell title="" :value="detail.card"></mt-cell>
		<div class="field-warn"></div>
		<mt-field v-model="detail.card1" label="CVN2" placeholder=""></mt-field>
		<mt-field v-model="detail.date" label="" placeholder=""></mt-field>
		<mt-field v-model="detail.rename" label="" placeholder=""></mt-field>
		<cell-password :options=" cellPassword "></cell-password>
		<div class="field-action">
			<mt-button @click="showDialogSmsVerify" type="primary"></mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			detail: {
				card: '',
				card1: '',
				date: '',
				rename: ''
			},
			cellPassword: {
                title: '',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '',
                btnConfirmText: '',
                btnGetText: ''
            }
		};
	},
	methods: {
		showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.addCredit();
            }, (data) => {
            });
        },
        addCredit() {
        	service.addCredit()
        	.then((data) => {
        		this.$router.push({name: 'MyBankCardCreditSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.detail.card = this.$route.params.card;
	}
};

</script>


