import ajax from '@/services/ajax';
import api from '@/services/api';

export default {
    // 
    getBankCardList: data => {
        return ajax.post(api.getBankCardList, data);
    },
    // 
    getBankCardDetail: data => {
        return ajax.post(api.getBankCardDetail, data);
    },
    // 
    deleteBankCard: data => {
        return ajax.post(api.deleteBankCard, data);
    },
    // 
    renameBankCard: data => {
        return ajax.post(api.renameBankCard, data);
    },
    // 
    defaultBankCard: data => {
        return ajax.post(api.defaultBankCard, data);
    },
    // 
	getBankCardAccount: data => {
        return ajax.post(api.getBankCardAccount, data);
    },
    // 
    addBankCardAccount: data => {
        return ajax.post(api.addBankCardAccount, data);
    },
    // 
    addBankCard: data => {
        return ajax.post(api.addBankCard, data);
    },
    // 
    getBankCreditList: data => {
        return ajax.postAfterLoginQueryList('', data, api.getBankCreditList);
    },
    // 
    addCredit: data => {
        return ajax.postAfterLoginUpdate('', data, api.addCredit);
    }
};



